@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap");

.font {
  position: relative;
  top: -300px;
  margin-left: 250px;
  /* font-family: fangsong;*/
  font-family: "Dancing Script", cursive;
  font-size: 64px;
  color: #10996d;
}

.project {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: #151226;

  /*background-color: #151226;
    width: 100%;
    height: 100vh;
    color: white;*/
}

.scene {
  width: 1000px;
  display: flex;
  margin-right: -20%;
  perspective: 800px;
  gap: 15%;
}
.scene .card {
  top: -80px;
  position: relative;
  width: 240px;
  height: 300px;
  color: white;
  cursor: pointer;
  transition: 1s ease-in-out;
  transform-style: preserve-3d;
}
.scene .card:hover {
  transform: rotateY(0.5turn);
}
.scene .card .card__face {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: 1s ease-in-out;
  -webkit-box-reflect: below 0
    linear-gradient(transparent, transparent, rgba(0, 0, 0, 0.4));
}
.scene .card .card__face img {
  width: 240px;
  height: 300px;
  -o-object-fit: cover;
  object-fit: cover;
}
.scene .card .card__face--back {
  transform: rotateY(0.5turn);
}

.icon {
  position: relative;
  cursor: pointer;
  top: -250px;
  justify-content: space-between;
  margin: -15px;
}

.icon::before {
  content: attr(data-tooltip);
  position: absolute;
  /* bottom: 100%;*/
  top: 40px;
  left: 50%;
  transform: translateX(-50%);
  background-color: darkcyan;
  color: #fff;
  padding: 5px 10px;
  border-radius: 5px;
  opacity: 0;
  transition: opacity 0.3s;
}

.icon::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  border-width: 10px 10px 0 10px;
  border-style: solid;
  border-color: white transparent transparent transparent;
}
.icon ::after {
  opacity: 1;
}
.icon:hover::before {
  opacity: 1;
}
.bounce:hover {
  animation: bounce 0.3s ease-in-out;
}
@keyframes bounce {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@media only screen and (max-width: 1100px) {
  .right {
    top: -170px;
  }
}

@media only screen and (max-width: 600px) {
  .navbar-right a {
    margin: 0 4px;
    padding: 12px 5px;
  }

  .header-name {
    font-size: 16px;
  }
}

@media only screen and (max-width: 700px) {
  .container-word {
    flex-direction: column;
  }

  .container_outer_img {
    margin: 20px;
  }

  .container_content {
    width: auto;
    margin-top: 20px;
  }

  .container_content_inner {
    width: auto;
    margin: 0px;
    padding: 10px;
  }

  .scene {
    flex-direction: column;
    margin: 0;
    align-items: center;
  }

  .about {
    background-color: #151226;
    width: 100%;
    height: 100vh;
    color: white;
    overflow: scroll;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding-bottom: 100px;
  }
  .paragraph {
    width: 90%;
    position: unset;
    margin: 0;
    word-spacing: normal;
    margin-top: 30px;
  }

  .right {
    position: unset;
    width: 300px;
    height: 262px;
    margin: 0;
    margin-top: 20px;
  }

  .font {
    position: unset;
    margin: 0;
    margin-top: 15px;
  }

  .icon {
    position: unset;
    margin-top: 20px;
  }

  .style {
    position: unset;
    margin: 0;
    margin-top: 20px;
  }

  .navbar {
    padding: 10px;
  }
}
